import React, { Component } from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import { withStyles } from '@material-ui/core/styles';

import PubNubReact from 'pubnub-react';

const styles = {
  container: {
    display: 'flex',
    flexWrap: 'wrap',
  },
};

class App extends Component {

  constructor(props) {
    super(props);
    this.pubnub = new PubNubReact({
        publishKey: 'pub-c-af9e408a-d4a8-473c-b591-81402cdf9aaf',
        subscribeKey: 'sub-c-7e76d5bc-2658-11e9-9508-c2e2c4d7488a'
    });
    this.state = {
      colour: "white"
    }
    this.pubnub.init(this);
  }

  componentDidMount() {
    this.pubnub.subscribe({
        channels: ['colour-demo'],
        withPresence: true
    });

    this.pubnub.getMessage('colour-demo', (msg) => {
        this.setState({colour:msg.message.colour});
    });
  }

  render(){
    return(
        <div style={{background: this.state.colour, width: "100%", height: "100vh"}}/>
      );
    }
}

const ChatComponent = withStyles(styles)(App);

ReactDOM.render(<ChatComponent />, document.getElementById('root'));


